<template>
    <div>
      <div class="aside-sticky-block">
        <slot></slot>
      </div>
    </div>
</template>


<script>
    import { mapState } from 'vuex'

    export default {
        name: "AsideSticky",
        components: {

        },
        props: {
            marginTop: {
               default() {
                   return 24;
               }
            },
            marginBottom: {
               default() {
                   return 24;
               }
            },

        },
        data() {
            return {
              aside: null,
              newBlock: null,
              fixedMargin: null,
              z: null,
            }
        },
        mounted() {
          let self = this;
          setTimeout(function(){
              self.aside = document.querySelector('#aside-sticky');
              window.addEventListener('scroll', self.ascroll, false);
              document.body.addEventListener('scroll', self.ascroll, false);
          }, 10);
        },
        beforeDestroy(){
          window.removeEventListener('scroll', this.ascroll, false);
          document.body.removeEventListener('scroll', this.ascroll, false);
        },
        watch: {
          '$route'(to,from){
              if(this.newBlock){
                  this.newBlock.className = 'stop';
                  this.newBlock.style.top = '0px';
              }
          }
        },
        methods: {
          ascroll() {
          	if(!document.querySelector('#aside-sticky')){
          		return;
          	}
          	var Ra = this.aside.getBoundingClientRect(),
          			R1bottom = document.querySelector('.container-scroll').getBoundingClientRect().bottom;
          	if (Ra.bottom < R1bottom) {
          		if (this.newBlock == null) {
          			var Sa = getComputedStyle(this.aside, ''), s = '';
          			for (var i = 0; i < Sa.length; i++) {
          				if (Sa[i].indexOf('overflow') == 0 || Sa[i].indexOf('padding') == 0 || Sa[i].indexOf('border') == 0 || Sa[i].indexOf('outline') == 0 || Sa[i].indexOf('box-shadow') == 0 || Sa[i].indexOf('background') == 0) {
          					s += Sa[i] + ': ' +Sa.getPropertyValue(Sa[i]) + '; '
          				}
          			}
          			this.newBlock = document.createElement('div');
          			this.newBlock.className = "stop";
          			this.newBlock.style.cssText = s + ' box-sizing: border-box; width: ' + this.aside.offsetWidth + 'px;';
          			this.aside.insertBefore(this.newBlock, this.aside.firstChild);
          			var l = this.aside.childNodes.length;
          			for (var i = 1; i < l; i++) {
          				this.newBlock.appendChild(this.aside.childNodes[1]);
          			}
          		}
          		var Rb = this.newBlock.getBoundingClientRect(),
          				Rh = Ra.top + Rb.height,
          				W = document.documentElement.clientHeight,
          				R1 = Math.round(Rh - R1bottom),
          				R2 = Math.round(Rh - W);
          		if (Rb.height > W) {
          			if (Ra.top < this.fixedMargin) {  // скролл вниз
          				if (R2 + this.marginBottom > R1) {  // не дойти до низа
          					if (Math.floor(Rb.bottom - W + this.marginBottom) <= 0) {  // подцепиться
          						this.newBlock.className = 'sticky';
          						this.newBlock.style.top = W - Rb.height - this.marginBottom + 'px';
          						this.z = this.marginBottom + Ra.top + Rb.height - W;
          					} else{
          						this.newBlock.className = 'stop';
          						this.newBlock.style.top = - this.z + 'px';
          					}
          				} else {
          					this.newBlock.className = 'stop';
          					this.newBlock.style.top = - R1 +'px';
          					this.z = R1;
          				}
          			} else {  // скролл вверх
          				if (Ra.top - this.marginTop < 0) {  // не дойти до верха
          					if (Rb.top - this.marginTop >= 0) {  // подцепиться
          						this.newBlock.className = 'sticky';
          						this.newBlock.style.top = this.marginTop + 'px';
          						this.z = Ra.top - this.marginTop;
          					} else {
          						this.newBlock.className = 'stop';
          						this.newBlock.style.top = - this.z + 'px';
          					}
          				} else {
          					this.newBlock.className = '';
          					this.newBlock.style.top = '';
          					this.z = 0;
          				}
          			}
          			this.fixedMargin = Ra.top;
          		} else {
          			if ((Ra.top - this.marginTop) <= 0) {
          				if ((Ra.top - this.marginTop) <= R1) {
          					this.newBlock.className = 'stop';
          					this.newBlock.style.top = - R1 +'px';
          				} else {
          					this.newBlock.className = 'sticky';
          					this.newBlock.style.top = this.marginTop + 'px';
          				}
          			} else {
          				this.newBlock.className = '';
          				this.newBlock.style.top = '';
          			}
          		}
          		let self = this;
          		window.addEventListener('resize', function() {
          			self.aside.children[0].style.width = getComputedStyle(self.aside, '').width
          		}, false);
          	}
          }
        }
    }
</script>

