<template>
    <div class="content">
        <div class="main-title">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h1 text-left">Вопросы и ответы</div>
            </div>
            <div class="main-title__right"></div>
        </div>


        <section class="container">
            <section class="container__left container-scroll">
                <div class="faq-item" v-for="item in items" :key="item.id">
                    <div class="h2">{{ item.name }}</div>
                    <p v-html="item.description"></p>
                </div>
            </section>
            <!-- end container__left -->


        <aside-sticky :marginTop="24" :marginBottom="24">
            <aside class="aside aside--right">
                <div class="aside__sticky" id="aside-sticky">
                    <div class="faq-add">
                        <svg class="big-icon icon">
                          <use xlink:href="@/assets/img/big-icons-sprite.svg#emoji-faq"></use>
                        </svg>
                        <div class="title">Задать вопрос</div>
                        <div class="text">
                            <textarea class="inp full" @focus="quest_error = false" :class="{error: quest_error}" v-model="questText" v-bind:style="{ height: '264px' }" placeholder="Например, «А бесплатные купоны – это вообще законно?!»"></textarea>
                        </div>

                        <div class="bottom">
                            <button class="btn size-sm" @click="sendQuest()">Отправить</button>
                        </div>
                    </div>
                </div>
            </aside>
            <!-- end aside aside--right -->
        </aside-sticky>
        </section>
        <!-- end container -->

        <popup :show="popups.newQuest.show" :name="popups.newQuest.name">
            <new-quest></new-quest>
        </popup>

    </div>
</template>



<script>
    import Popup from '@/components/modal/Popup.vue'
    import NewQuest from '@/components/modal/NewQuest.vue'
    import AsideSticky from '@/components/AsideSticky.vue'
    import {mapState} from 'vuex'

    export default {
        metaInfo() {
            return {
                title: 'Вопросы и ответы',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Faq",
        components: {
            Popup,
            NewQuest,
            AsideSticky,
        },
        data: function() {
            return {
                questText: '',
                quest_error: false,
                send_quest_success: true
            }
        },
        created() {
            this.$store.dispatch('faq/getList');
        },
        mounted: function () {
            ym(39511350, 'hit', this.$route.path, {title: 'Вопросы и ответы | ZemSkidki.ru'});
        },
        methods: {
            sendQuest: function() {
                let  self = this;
                self.quest_error = false;
                if (self.questText === '') {
                    setTimeout(function () {
                        self.quest_error = true;
                    }, 300);
                } else {
                    self.quest_error = false;
                    this.$store.dispatch('faq/sendQuest', {quest: self.questText}).then(()=>{
                        self.questText = '';
                    });
                    self.newQuestModalOpen();
                }
            },
            newQuestModalOpen: function () {
                this.send_quest_success = false;
                this.$store.dispatch('popups/open', {name: this.popups.newQuest.name});
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
            items() {
                this.send_quest_success = true;
                return this.$store.getters['faq/getList']();
            },
        }
    }
</script>
